import React from 'react';
import { useParams } from 'react-router-dom';
import { useAppSelector } from '../../hooks/useApp';

import ingredientStyles from './ingredient-page.module.css';

function IngredientPage() {
  const { id } = useParams();
  const data = useAppSelector((store) => store.ingredientsReducer.data);
  let ingredient = useAppSelector(
    (store) => store.ingredientsReducer.currentIngredient
  );
  if (ingredient === null) {
    ingredient = data.find((el) => el._id === id)!;
  }

  return (
    <div className={ingredientStyles.container}>
      {data.length && (
        <>
          <p className="text text_type_main-large mt-10 mr-10 ml-10">
             
          </p>
          <img
            src={ingredient.image_large}
            alt={ingredient.name}
            className={`${ingredientStyles.image} mb-4`}
          />
          <p className="text text_type_main-medium mb-8">{ingredient.name}</p>
          <div className={ingredientStyles.details}>
            <p className="text text_type_main-default text_color_inactive">{`,  ${ingredient.calories}`}</p>
            <p className="text text_type_main-default text_color_inactive">{`,  ${ingredient.proteins}`}</p>
            <p className="text text_type_main-default text_color_inactive">{`,  ${ingredient.fat}`}</p>
            <p className="text text_type_main-default text_color_inactive">{`,  ${ingredient.carbohydrates}`}</p>
          </div>
        </>
      )}
    </div>
  );
}

export default IngredientPage;
